@import "../colours";
@import "../variables";

.PageWrapperContainer {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;

  .TopShadow {
    width: 200%;
    height: 50px;
    position: absolute;
    top: -50px;
    // box-shadow: 0px 50px 100px 20px $Colour_Gray;
    box-shadow: 0px 50px 100px 0px $Colour_BlackTransparent;
    z-index: 2;
    visibility: visible;
  }
  .PageWrapperContainer_Children_ScrollContainer {
    // position: relative;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    visibility: hidden;

    .PageWrapperContainer_Children {
      margin-left: 20px;
      // margin-right: 10px;
      height: 100%;
    }
  }
}

//Enable scrollbar visibility from parent DIV
.PageWrapperContainer_Children,
.PageWrapperContainer_Children_ScrollContainer:hover,
.PageWrapperContainer_Children_ScrollContainer:focus {
  visibility: visible;
}

@media (max-width: map-get($screen-size-width, sm)) {
  .PageWrapperContainer {
    .TopShadow {
      box-shadow: 0px 10px 100px 0px $Colour_Black;
    }

    .PageWrapperContainer_Children_ScrollContainer {
      .PageWrapperContainer_Children {
        margin-left: 0px;
        margin-right: 0px;
        overflow-y: auto;
      }
    }
  }
}

h4 {
  margin-left: 10px;
}
