@import "../colours";

#map {
  position: relative;
  display: block;
  width: 100%;
  height: 300px;
  overflow: hidden;
  // margin-top: 20px;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0px;
    // frameborder: "0";
  }

  #maploader {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: $Colour_White;
    // background-color: $Colour_LightGray;
    // opacity: 0.5;
  }
}

.ContactDetails {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 10px;

  p {
    color: $Colour_White;
    padding-left: 10px;
  }

  ion-icon {
    width: 45px;
    height: 45px;
    color: $Colour_Primary;
  }
}
