@import "./colours";
@import "./variables";

#mainContainer {
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
  background-color: $Colour_DarkGray;
  justify-content: center;
  align-items: center;

  #leftNav {
    min-height: 50vh;
    min-width: auto;
    display: flex;
    flex-direction: column;
    flex-shrink: 1;

    background-color: $Colour_Gray;
    box-shadow: inset 0px 0px 20px 10px $Colour_BlackTransparent;
  }

  #centerPerson {
    height: 95vh; //850px; //90vh;
    width: 23rem;
    min-width: 15rem;
    z-index: 1;
  }

  #rightPages {
    height: 85vh;
    width: 50rem;
    min-width: 20rem;
    background-color: $Colour_Gray;
    box-shadow: inset 0px 0px 100px 50px $Colour_BlackTransparent;
    overflow-y: auto;
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  #mainContainer {
    flex-direction: column;

    #leftNav {
      // display: none;
      // position: relative;
      // top: -50px;
      min-width: 100vw;
      min-height: 1vh;
      border-bottom: solid 1px $Colour_LightGray;
    }

    #centerPerson {
      display: none;
    }

    #rightPages {
      height: 100vh;
      width: 100vw;
    }
  }
}
