@import "../colours";
@import "../variables";

.TrainingListContainer {
  //   width: 100%;
  margin: 10px;
  display: flex;
  flex-direction: column;

  .Training_Headers_Container {
    display: flex;
    flex-direction: row;
    // border-bottom: solid 1px $Colour_LightGray;

    // justify-content: ;

    .Training_Header_Container {
      display: flex;
      flex-direction: column;
      padding-left: 5px;

      .Training_Header_1 {
        color: $Colour_White;
      }

      .Training_Header_2 {
        color: $Colour_Primary;
        margin-left: 10px;
      }
    }
  }

  .TrainingList_Modules {
    color: $Colour_LightGray;
    list-style-type: square;
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  .TrainingListContainer {
    width: 90%;
  }
}
