@import "../colours";
@import "../variables";

$NavContainerSize: 45px;
$NavItemSizeOffset: 10px;
$NavItemSpaceAround: 10px;

.LinksContainer {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  // justify-content: center;

  .LinkContainer_Inner {
    display: flex;
    align-items: center;
    justify-content: center;

    height: $NavContainerSize;
    width: $NavContainerSize;
    margin: $NavItemSpaceAround;

    .NavItem {
      ion-icon {
        height: $NavContainerSize - $NavItemSizeOffset;
        width: $NavContainerSize - $NavItemSizeOffset;
        color: $Colour_LightGray;
        transition: all 0.3s;
      }
    }

    .NavItemActive {
      ion-icon {
        height: $NavContainerSize;
        width: $NavContainerSize;
        color: $Colour_Primary;
        transition: all 0.3s;
      }
    }
  }

  .LinkContainer_Inner:first-child {
    display: none;
  }

  // .NavItem:nth-last-child(-n + 2) {
  //   ion-icon {
  //     color: $Colour_Secondary;
  //   }
  // }
}

@media (max-width: map-get($screen-size-width, sm)) {
  // $NavContainerSize: $NavContainerSize - 35px;

  .LinksContainer {
    flex-flow: row;
    justify-content: space-between;

    .LinkContainer_Inner:first-child {
      display: block;
    }
  }
}
