@import "../colours";
@import "../variables";

$ContainerWidth: 15rem;

.SkillListContainer {
  width: $ContainerWidth;
  .SkillListHeader {
    color: $Colour_Primary;
    border-bottom: solid 1px $Colour_LightGray;
    margin-bottom: 0px;
    font-size: $ContainerWidth * 0.085;
  }

  ul {
    li {
      color: $Colour_LightGray;
    }
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  .SkillListContainer {
    width: 90%;
  }
}
