@import "../../colours";
@import "../../variables";

.TabsEncapsulator_Container {
  position: relative;
  // height: 100%;
  width: 100%;

  .TabsEncapsulator_TabsContainer {
    display: flex;
    justify-content: center;
    align-items: stretch;
    min-height: 40px;
    width: 100%;
    // border-bottom: solid 1px gray;
  }
  .TabsEncapsulator_ContentWrapper {
    display: none;
    margin-top: 10px;
    // background-color: $Colour_DarkGray;
    // border-top: solid 1px gray;
    // position: relative;
    // top: -1px;
    // height: 100%;
  }
  .TabsEncapsulator_ContentWrapper--Active {
    display: block;
  }
}

.TabElement_Container {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  color: $Colour_LightGray;
  text-align: center;
  padding-right: 5px;
  padding-left: 5px;
  // z-index: 1;
  border-bottom: solid 1px gray;

  // margin-left: 1px;
  // margin-right: 1px;
  // border-left: solid 1px gray;
  // border-right: solid 1px gray;
  // border-top: solid 1px gray;
  // transition: all 0.3s;
}

.TabElement_Container--Active {
  border-bottom: solid 1px transparent;
  border-left: solid 1px gray;
  border-right: solid 1px gray;
  border-top: solid 1px gray;
  color: $Colour_Primary;
  // background-color: $Colour_DarkGray;
  // padding-right: 0px;
  // padding-left: 0px;
  // text-decoration-line: underline;
}

@media (max-width: map-get($screen-size-width, sm)) {
  .TabElement_Container {
    // font-size: calc(100vw * 0.04);
    font-size: calc(1vw + 10px);
  }
}
