@import "../colours";
@import "../variables";

.EducationListContainer {
  //   width: 100%;
  margin: 10px;
  display: flex;
  flex-direction: column;

  .Education_Headers_Container {
    display: flex;
    flex-direction: row;
    border-bottom: solid 1px $Colour_LightGray;

    // justify-content: ;

    .Education_Header_Container {
      display: flex;
      flex-direction: column;
      padding-left: 5px;

      .Education_Header_1 {
        color: $Colour_White;
      }

      .Education_Header_2 {
        color: $Colour_Primary;
        margin-left: 10px;
      }
    }
  }

  .EducationList_Modules {
    color: $Colour_LightGray;
    list-style-type: square;
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  .EducationListContainer {
    width: 90%;
  }
}
