@import "../colours";
@import "../variables";

.PageHeaderContainer {
  display: flex;
  flex-flow: column;
  color: $Colour_White;
  border-bottom: solid 1px $Colour_LightGray;
  // padding-bottom: 10px;
  margin-bottom: 10px;
  overflow: hidden;
  position: sticky;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 100;
  box-shadow: inset 0px 0px 100px 30px $Colour_GrayHeader;
  padding-left: 10px;
  padding-right: 10px;

  .Header {
    // margin: 10px;
    display: flex;
    flex-direction: row;
    flex-flow: row;
    align-items: center;

    ion-icon {
      width: 35px;
      height: 35px;
    }
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  .PageHeaderContainer {
    border-bottom: none;
    position: relative;
    box-shadow: none;
  }
}
