@import-normalize;
@import "./colours";
@import "./variables";

body {
  background-color: $Colour_Black;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

// span {
//   pointer-events: none;
// }

ul {
  margin-top: 5px;
  list-style: square;
}

h1,
h2,
h3,
h4,
h5,
h6,
h7 {
  color: $Colour_White;
  word-wrap: break-word;
  margin-top: 10px;
  margin-bottom: 10px;
}

.Divider {
  // position: absolute;
  height: 1px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  // left: 0px;
  // right: 0px;
  border-bottom: dashed 1px $Colour_LightGray;
}

@media (max-width: map-get($screen-size-width, sm)) {
  body {
    font-size: 16px;
    // font-size: auto;
  }
}
