@import "../colours";
@import "../variables";

/***MOBILE CONSTANTS***/
$MOBILE_scaleMultiplier: 0.3;
//Frame
$MOBILE_FrameWidth: 826px * $MOBILE_scaleMultiplier;
$MOBILE_FrameHeight: 1718px * $MOBILE_scaleMultiplier;
//Inner
$MOBILE_paddingTop: 192px * $MOBILE_scaleMultiplier;
$MOBILE_paddingLef: 38px * $MOBILE_scaleMultiplier;
$MOBILE_paddingRight: 38px * $MOBILE_scaleMultiplier;
$MOBILE_paddingBottom: 191px * $MOBILE_scaleMultiplier;
//Header
$MOBILE_HeaderTopOffset: 15px;
//Controls
$MOBILE_controlPanelHeight: 150px * $MOBILE_scaleMultiplier;
$MOBILE_buttonSize: 120px * $MOBILE_scaleMultiplier;

/***WEB CONSTANTS***/
$WEB_scaleMultiplier: 1;
//Frame
$WEB_FrameWidth: 719px * $WEB_scaleMultiplier;
$WEB_FrameHeight: 438px * $WEB_scaleMultiplier;
$WEB_FramePaddingTop: ($WEB_FrameHeight / $WEB_FrameWidth * 100%);
//Inner
// $WEB_paddingTop: 71px * $WEB_scaleMultiplier;
$WEB_paddingTop: (71px / $WEB_FrameHeight * 100%) * $WEB_scaleMultiplier;
$WEB_paddingLef: 1px * $WEB_scaleMultiplier;
$WEB_paddingRight: 1px * $WEB_scaleMultiplier;
$WEB_paddingBottom: 51px * $WEB_scaleMultiplier;
// $WEB_paddingBottom: (51px / $WEB_FrameHeight * 100%) * $WEB_scaleMultiplier;

//Header
$WEB_HeaderTopOffset: 15px;
//Controls
$WEB_controlPanelHeight: 150px * $WEB_scaleMultiplier;
$WEB_buttonSize: 35px * $WEB_scaleMultiplier;

.ProjectDisplay_Container {
  // background-color: red;
  display: flex;
  position: relative;
  // background-image: url("../Assets/phoneframe.png");
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  margin: 20px;
  // box-sizing: border-box;

  .ProjectHeader {
    position: absolute;
    width: 100%;
    text-align: center;
    color: $Colour_White;
    font-size: 2rem;
  }

  .LoadingText {
    position: absolute;
    top: 50%;
    bottom: 50%;
    text-align: center;
    width: 100%;
    color: $Colour_White;
  }

  .ProjectDisplay_Background {
    position: absolute;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    pointer-events: none;

    // top: 0px;
    // bottom: 0px;
    // left: 0px;
    // right: 0px;

    transition: opacity 0.3s;
  }

  .Controls {
    background-color: $Colour_Gray;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    padding: 2px;

    .Controls_Progress {
      color: $Colour_White;
      text-align: center;

      width: 50%;
    }

    .Controls_Button {
      color: $Colour_Secondary;
      cursor: pointer;

      ion-icon {
        width: 100%;
        height: 100%;
      }
    }
  }
}

/*******************MOBILE****************/
.ProjectDisplay_Container--MOBILE {
  width: $MOBILE_FrameWidth;
  height: $MOBILE_FrameHeight;
}
.ProjectDisplay_Background--MOBILE {
  top: $MOBILE_paddingTop;
  left: $MOBILE_paddingLef;
  bottom: $MOBILE_paddingBottom;
  right: $MOBILE_paddingRight;
}
.ProjectHeader--MOBILE {
  top: $MOBILE_HeaderTopOffset;
  font-size: 1.5rem !important;
}
.Controls--MOBILE {
  bottom: 12px;
  left: 10px;
  right: 10px;

  .Controls_Progress {
  }

  .Controls_Button {
    width: $MOBILE_buttonSize;
    height: $MOBILE_buttonSize;
  }
}

/*******************WEB****************/
.ProjectDisplay_Container--WEB {
  width: $WEB_FrameWidth;
  // height: $WEB_FrameHeight;
  padding-bottom: $WEB_FramePaddingTop;
}
.ProjectDisplay_Background--WEB {
  top: $WEB_paddingTop;
  left: $WEB_paddingLef;
  bottom: $WEB_paddingBottom;
  right: $WEB_paddingRight;
}
.ProjectHeader--WEB {
  font-size: calc(1rem + 0.4vw) !important;
}

.Controls--WEB {
  // margin: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
  .Controls_Progress {
  }

  .Controls_Button {
    width: $WEB_buttonSize;
    height: $WEB_buttonSize;
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  .ProjectHeader--WEB {
    font-size: calc(1rem + 0.2vw) !important;
  }
}
