@import "../colours";
$navItemSize: 50px;

.MeContainer {
  // font-size: 20px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: $Colour_Gray;
  box-shadow: inset 0px 0px 100px 100px $Colour_BlackTransparent;
  // background-image: url("../Assets/niko_img.jpg");
  background-image: url("../Assets/me.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: lighten;
  text-align: center;
  align-items: center;

  #devInfo {
    display: flex;
    flex-direction: column;
    text-align: left;
  }

  // h1,
  h2,
  h4 {
    color: $Colour_Primary;
    margin: 0px;
    padding: 0px;
    line-height: 25px;
  }

  .WARNING {
    display: flex;
    align-items: center;
    color: $Colour_Danger;
    a {
      width: 30px;
      height: 30px;

      ion-icon {
        width: 100%;
        height: 100%;
      }
    }
  }

  .NavItem {
    height: $navItemSize;
    width: $navItemSize;
    margin: 10px;

    a {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      width: 100%;
      height: 100%;
      color: $Colour_Black;
      font-weight: bold;
      text-decoration: none;

      ion-icon {
        width: 100%;
        height: 100%;
        color: $Colour_Secondary;
      }

      .Descr {
        position: absolute;
        pointer-events: none;
        font-size: $navItemSize / 2.8;
        padding-bottom: 5px;
        color: $Colour_Black;
      }
    }
  }
}

.Complete {
  text-decoration: line-through;
}
