@import "../colours";
@import "../variables";

.ProjectsContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  // align-content: center;
}

h3 {
  color: $Colour_Primary;
}

.WorkstationContainer {
  display: flex;
  flex-flow: column;
  align-items: center;

  .WorkstationBackground {
    // margin-top: 5px;
    width: 95%;
    // height: 50%;
    // padding-bottom: calc(100% / 50vh);
    // background-color: red;
    // background-image: url("../Assets/me.png");
    // background-image: url("./Assets/Projects/Workstation/01.JPG");
    background-repeat: no-repeat;
    background-position: top;
    background-size: contain;
  }

  .SpecList {
    width: 95%;
  }
}

.SpecList {
  // padding-top: 5px;
  // font-size: calc(1vw + 9px);
  padding-left: 10px;
  color: $Colour_White;
  list-style-type: square;
  display: flex;
  flex-flow: row;

  .SpecList_ItemsContainer {
    display: flex;
    flex-flow: column;

    .SpecList_Item_1 {
      color: $Colour_Secondary;
      padding-bottom: 5px;
    }
    .SpecList_Item_2 {
      padding-left: 10px;
      padding-bottom: 5px;

      ul {
        padding-left: 1rem;
      }
    }
  }
}

.NasContainer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  .NasPicsContainer {
    display: flex;
    flex-flow: column wrap;

    justify-content: space-around;

    img {
      // margin: 10px;
      margin-bottom: 10px;
      height: 30vh;
    }
  }

  .SpecList {
    width: 300px;
    flex-grow: 1;
  }
}

.HassContainer {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  // align-items: center;
  justify-content: center;

  .HassPicsContainer {
    // display: flex;

    img {
      // margin: 10px;
      width: 300px;
      // flex-grow: 0.1;
      // flex-basis: 100px;
    }
  }

  .SpecList {
    max-width: 400px;
    // flex-grow: 0.1;
  }
}

@media (max-width: map-get($screen-size-width, sm)) {
  // .SpecList {
  //   font-size: calc(1vw + 9px);
  // }

  .NasContainer {
    .NasPicsContainer {
      img {
        height: 80vw;
      }
    }
  }

  .HassContainer {
    .HassPicsContainer {
      img {
      }
    }
  }
}
